// components/AdminStateExplorer.tsx

// Fixes:
// 1. Module not found error (by moving it back to the expected path /components)
// 2. Circular clipping of the globe (by removing rounded-full and overflow-hidden)
// 3. Header/Footer UI (by using a basic Tailwind container)

import { useState } from "react";
import GlobeStates from "./GlobeStates";
import { JURISDICTIONS, DEFAULT_JURISDICTION } from "../lib/jurisdictions";
import { useGlobeStore } from "../lib/store";

// Helper component for the selector menu
const JurisdictionSelector = ({
  selectedRegion,
  setSelectedRegion,
}: {
  selectedRegion: string;
  setSelectedRegion: (region: string) => void;
}) => (
  <div className="flex justify-center p-4 bg-slate-800 rounded-lg shadow-xl">
    <select
      className="block w-full max-w-sm rounded-md border-0 py-1.5 pl-3 pr-10 text-slate-100 bg-slate-700 ring-1 ring-inset ring-slate-600 focus:ring-2 focus:ring-indigo-500 sm:text-sm sm:leading-6"
      value={selectedRegion}
      onChange={(e) => setSelectedRegion(e.target.value)}
    >
      <option value="ALL">All Regions (All {JURISDICTIONS.length} Countries)</option>
      {Array.from(new Set(JURISDICTIONS.map((j) => j.region)))
        .sort()
        .map((region) => (
          <option key={region} value={region}>
            {region.charAt(0).toUpperCase() + region.slice(1)} Region
          </option>
        ))}
    </select>
  </div>
);

// Main Component
export default function AdminStateExplorer() {
  const { selectedStateId } = useGlobeStore();
  const [selectedRegion, setSelectedRegion] = useState(DEFAULT_JURISDICTION.region);

  return (
    <div className="flex flex-col h-screen overflow-hidden bg-slate-900">
      {/* The top-level container is for the entire application view. 
        It replaces the complex header/footer layout to focus on the map functionality. 
      */}
      <div className="flex-shrink-0">
        <JurisdictionSelector
          selectedRegion={selectedRegion}
          setSelectedRegion={setSelectedRegion}
        />
      </div>

      <main className="flex-1 flex min-h-0 relative p-4">
        {/* Globe Container Fixes: 
            1. Removed 'rounded-full' to fix circular clipping.
            2. Removed 'overflow-hidden' to allow globe to render outside the box on zoom/rotation.
            3. Changed 'aspect-square' to 'h-full w-full' for full container use.
        */}
        <div className="relative h-full w-full mx-auto max-w-7xl">
          <GlobeStates region={selectedRegion} />
          
          {/* Globe Shadow/Halo (Kept from original design) */}
          <div className="pointer-events-none absolute inset-0 rounded-lg shadow-[0_0_150px_100px_rgba(2,_6,_23,_1)] [transform:translateZ(-100px)]"></div>
          
          {/* State/Polygon Info Panel (Kept from original design) */}
          {selectedStateId && (
            <div className="absolute top-4 right-4 z-10 p-4 bg-slate-800 text-white rounded-lg shadow-xl">
              <p className="font-semibold">Selected State/Province:</p>
              <p className="text-xl text-emerald-400">{selectedStateId}</p>
            </div>
          )}
        </div>
      </main>

      <footer className="flex-shrink-0 p-2 text-center text-xs text-slate-500 bg-slate-900 border-t border-slate-800">
        Cartfax Admin Explorer - Viewing {selectedRegion} Region
      </footer>
    </div>
  );
}